<template>
  <div class="r-note-item" @click="handleClickNote(noteItem)">
    <h3>{{ noteItem.title }}</h3>
    <!-- <div class="item_info" v-once v-html="transformValue(noteItem.content)"></div> -->
    <r-quill-editor ref="REditor" :funType="funType" :content="noteItem.content" :is-class="false"
      :is-edit="false" :max-height="maxHeight">
    </r-quill-editor>
    <div class="item-foot">
      <span class="foot-info">{{ noteItem.CHName }}</span>
      <span class="foot-remark">
        {{ noteItem.createTime | formatDate(noteItem.createTime) }}
      </span>
    </div>
  </div>
</template>
<script>
import RQuillEditor from "@/components/r-quill-editor";
import { transformValue } from "@/utils/index";
import { ModulePicture } from "@/utils/enum";
export default {
  name: "note-item",
  components: { RQuillEditor },
  props: {
    noteItem: {
      type: Object
    }
  },
  data() {
    return {
      funType: ModulePicture.StudentNote,
      maxHeight: null
    };
  },
  methods: {
    // 图片路径转换
    transformValue(value) {
      return transformValue("show", value, this.$mainHttp.resourse.picture);
    },
    handleClickNote(item) {
      this.$emit("select", item);
    }
  }
};
</script>>



